.nav {
  display: flex;
  height: 100%;
  &__list {
    display: flex;
    position: relative;
    justify-content: space-between;
    margin: 0;
    padding: 0;
  }
  &__item {
    list-style-type: none;
    margin-right: 20px;
    line-height: 60px;
    &:last-child {
      margin: 0;
    }
  }
  &__link {
    display: block;
    box-sizing: border-box;
    height: 100%;
    text-decoration: none;
    text-transform: uppercase;
    color: black;
    transition: color 0.5s;
    &:hover {
      color: #00acdc;
    }
  }
}

.is-active {
  color: #00acdc;
  &::after {
    content: "";
    position: relative;
    display: block;
    bottom: 4px;
    width: 100%;
    height: 4px;
    background: #00acdc;
    border-radius: 8px;
  }
}
